import _ from 'lodash';
import mapErrorCode from './map-error-code.json';
import { formatNumber } from "accounting";

var logDebugFlag = true;
export function logDebug(step, logValue) {
  if(logDebugFlag)
    console.log(step, logValue);
}

export function logDebug2Val(step, logValue1, logValue2) {
  if(logDebugFlag)
    console.log(step, logValue1, logValue2);
}

export function checkIsServer() {
  try {
    return !window;
  } catch (e) {
    return true;
  }
}

export const chuyenSoSangChuyenDoDai = (number,len) =>{
  const e = '000000';
  const s = e + number;
  return s.substr(s.length-len,len);
};

export const formatPrice = (price, numberAfterDot = 2) => {
  if (typeof price !== "number") return "";
  return formatNumber(price / 1000, numberAfterDot);
};
export const formatNumber0 = (price) => {
  if (typeof price !== "number") return "";
  return formatNumber(price, 0);
};

export const replaceAll = (str, search, replacement) => {
  let newStr = '';
  if (_.isString(str)) { // maybe add a lodash test? Will not handle numbers now.
    newStr = str.split(search).join(replacement);
  }
  return newStr;
};

export const correctTyping = (source) => {
  if (!source) return '';
  let result = replaceAll(source, '	', ' ');
  // result = replaceAll(result, ' ：', ' ：');
  // result = replaceAll(result, '「 ', '「');
  // result = replaceAll(result, ' 」', '」');
  // result = replaceAll(result, '. 」', '.」');
  // result = replaceAll(result, '.」', '」.');
  // result = replaceAll(result, '?', '? ');
  // result = replaceAll(result, '.', '. ');
  result = replaceAll(result, '   ', ' ');
  result = replaceAll(result, '  ', ' ');
  return result;
};

export const correctTypingChinese = (source) => {
  if (!source) return '';
  let result = replaceAll(source, ' ', '');
  result = replaceAll(result, '	', '');
  result = replaceAll(result, '   ', ' ');
  result = replaceAll(result, '  ', ' ');
  return result;
};

const chuanHoaText = (source) => {
  if (!source) return '';
  let result = replaceAll(source, '	', ' ');
  result = replaceAll(result, '。', '.');
  result = replaceAll(result, '，', ',');
  result = replaceAll(result, '  ', ' ');
  result = replaceAll(result, '  ', ' ');
  result = replaceAll(result, '   ', ' ');
  result = replaceAll(result, '	', ' ');
  result = replaceAll(result, ' ：', ':');
  result = replaceAll(result, '「 ', '「');
  result = replaceAll(result, '  」', ' 」');
  result = replaceAll(result, '. 」', '.」');
  result = replaceAll(result, '.」', '」.');
  result = replaceAll(result, '?', '? ');
  result = replaceAll(result, '.', '. ');
  return result;
};

export const xoaKiTuThuaCopyTuTrang = (chinesse) => {
  let chinesseNonSpace = replaceAll(chinesse, '  ', '');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '  ', '');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '  ', '');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '\n\n', '\n');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '\n\n', '\n');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '\n\n', '\n');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '始祖', 'tổ tiên');

  return chinesseNonSpace;
};
export const chuanHoaChinesseV2 = (chinesse) => {
  let chinesseNonSpace = replaceAll(chinesse, ' ', '');
  chinesseNonSpace = replaceAll(chinesseNonSpace, '  ', '');
  const result = [];
  const signsBef = '「(（';
  const signsAfter = '、，,:：;；.。」)）?！';
  let currentWord = '';
  let i = 0;
  for (i = 0; i <= chinesseNonSpace.length; i++) {
    if (signsBef.indexOf(chinesseNonSpace[i]) >= 0) {
      if (currentWord !== '') {
        result.push(currentWord);
      }
      currentWord = chinesseNonSpace[i];
    } else if (signsAfter.indexOf(chinesseNonSpace[i]) >= 0 &&
      i < chinesseNonSpace.length - 1 &&
      signsAfter.indexOf(chinesseNonSpace[i + 1]) >= 0) {
      currentWord += chinesseNonSpace[i] + chinesseNonSpace[i + 1];
      if (currentWord !== '') {
        result.push(currentWord);
      }
      currentWord = '';
      i++;
    } else if (signsAfter.indexOf(chinesseNonSpace[i]) >= 0) {
      currentWord += chinesseNonSpace[i];
      if (currentWord !== '') {
        result.push(currentWord);
      }
      currentWord = '';
    } else {
      if (currentWord !== '' && signsBef.indexOf(currentWord) < 0) {
        result.push(currentWord);
      }
      if (signsBef.indexOf(currentWord) >= 0) {
        currentWord += chinesseNonSpace[i];
      } else {
        currentWord = chinesseNonSpace[i];
      }
    }
  }

  return result;
};
export const chuanHoaChinneseNoSign = (source) => {
  let result = chuanHoaText(source);
  result = replaceAll(result, '、', '');
  result = replaceAll(result, '，', '');
  result = replaceAll(result, '「', '');
  result = replaceAll(result, '」', '');
  result = replaceAll(result, '(', '');
  result = replaceAll(result, ')', '');
  result = replaceAll(result, '（', '');
  result = replaceAll(result, '）', '');
  result = replaceAll(result, '.', '');
  result = replaceAll(result, ' ', '');
  result = replaceAll(result, '.', '');
  result = replaceAll(result, ',', '');
  result = replaceAll(result, '。', '');
  result = replaceAll(result, ':', '');
  result = replaceAll(result, '：', '');
  result = replaceAll(result, '：', '');
  result = replaceAll(result, '?', '');
  result = replaceAll(result, '!', '');
  result = replaceAll(result, '！', '');
  result = replaceAll(result, ';', '');
  result = replaceAll(result, '；', '');
  return result;
};

// 佛	說	："	人	人	都	可	以	成	佛。"
export const chuanHoaChinesse = (chinesse) => {
  const result = chuanHoaText(chinesse);
  return result.split(' ');
};

export const chuanHoaHanVietBacKinh = (source) => {
  let result = replaceAll(source, '\n', ' ');
  result = chuanHoaText(result);
  result = replaceAll(result, '「', '');
  result = replaceAll(result, '」', '');
  result = replaceAll(result, '.', '');
  result = replaceAll(result, ':', '');
  result = replaceAll(result, '。\n', '。 \n');
  result = replaceAll(result, '.\n', '. \n');

  return result.split(' ');
};

export function getMessageError(error) {
  const status =
    _.get(error, 'response.data.httpStatus') ||
    _.get(error, 'httpStatus') ||
    _.get(error, 'code');
  const message = mapErrorCode[status];
  const message1 = _.get(error, 'response.data.message');
  const message2 = _.get(error, 'message');
  return message || message1 || message2;
}

export const getSoundFile = (phonetic) => {
  if (!phonetic) return '';
  let fileName = phonetic.toLowerCase();
  fileName = fileName.replace(':', '');
  fileName = fileName.replace('.', '');
  fileName = fileName.replace(',', '');
  fileName = fileName.replace('?', '');
  fileName = fileName.replace('!', '');
  fileName = fileName.replace('！', '');
  fileName = fileName.replace('"', '');
  if (fileName.indexOf('ā') >= 0) {
    return `${fileName.replace('ā', 'a')}1.mp3`;
  } else if (fileName.indexOf('á') >= 0) {
    return `${fileName.replace('á', 'a')}2.mp3`;
  } else if (fileName.indexOf('ǎ') >= 0) {
    return `${fileName.replace('ǎ', 'a')}3.mp3`;
  } else if (fileName.indexOf('à') >= 0) {
    return `${fileName.replace('à', 'a')}4.mp3`;
  } else if (fileName.indexOf('ō') >= 0) {
    return `${fileName.replace('ō', 'o')}1.mp3`;
  } else if (fileName.indexOf('ó') >= 0) {
    return `${fileName.replace('ó', 'o')}2.mp3`;
  } else if (fileName.indexOf('ǒ') >= 0) {
    return `${fileName.replace('ǒ', 'o')}3.mp3`;
  } else if (fileName.indexOf('ò') >= 0) {
    return `${fileName.replace('ò', 'o')}4.mp3`;
  } else if (fileName.indexOf('ē') >= 0) {
    return `${fileName.replace('ē', 'e')}1.mp3`;
  } else if (fileName.indexOf('é') >= 0) {
    return `${fileName.replace('é', 'e')}2.mp3`;
  } else if (fileName.indexOf('ě') >= 0) {
    return `${fileName.replace('ě', 'e')}3.mp3`;
  } else if (fileName.indexOf('è') >= 0) {
    return `${fileName.replace('è', 'e')}4.mp3`;
  } else if (fileName.indexOf('ī') >= 0) {
    return `${fileName.replace('ī', 'i').replace('yi', 'i')}}1.mp3`;
  } else if (fileName.indexOf('í') >= 0) {
    return `${fileName.replace('í', 'i').replace('yi', 'i')}2.mp3`;
  } else if (fileName.indexOf('ǐ') >= 0) {
    return `${fileName.replace('ǐ', 'i').replace('yi', 'i')}3.mp3`;
  } else if (fileName.indexOf('ì') >= 0) {
    return `${fileName.replace('ì', 'i').replace('yi', 'i')}4.mp3`;
  } else if (fileName.indexOf('ū') >= 0) {
    return `${fileName.replace('ū', 'u').replace('wu', 'u')}1.mp3`;
  } else if (fileName.indexOf('ú') >= 0) {
    return `${fileName.replace('ú', 'u').replace('wu', 'u')}2.mp3`;
  } else if (fileName.indexOf('ǔ') >= 0) {
    return `${fileName.replace('ǔ', 'u').replace('wu', 'u')}3.mp3`;
  } else if (fileName.indexOf('ù') >= 0) {
    return `${fileName.replace('ù', 'u').replace('wu', 'u')}4.mp3`;
  } else if (fileName.indexOf('ü') >= 0) {
    return `${fileName.replace('ü', 'v').replace('yv', 'v')}1.mp3`;// ü
  } else if (fileName.indexOf('ǖ') >= 0) {
    return `${fileName.replace('ǖ', 'v').replace('yv', 'v')}1.mp3`;// ü
  } else if (fileName.indexOf('ǘ') >= 0) {
    return `${fileName.replace('ǘ', 'v').replace('yv', 'v')}1.mp3}2.mp3`;
  } else if (fileName.indexOf('ǜ') >= 0) {
    return `${fileName.replace('ǜ', 'v').replace('yv', 'v')}1.mp3}4.mp3`;
  } else if (fileName.indexOf('ǔ') >= 0) {
    return `${fileName.replace('ǔ', 'u').replace('yv', 'v')}1.mp3}3.mp3`;
  }
  return `${fileName}1.mp3`;
};

export function getErrorData(error) {
  try {
    return JSON.stringify(error);
  } catch (e) {
    return 'Không lấy được toàn bộ thông tin lỗi';
  }
}

export function isEmptyOrBlank(str) {
  return _.isEmpty(str.trim());
}

export function removeTags(content) {
  const regex = /(<([^>]+)>)/ig;
  return content.replace(regex, '');
}
